<!--
 * @作者： 丁亚辉
 * @日期： 2019/11/13 17:29
-->

<template>
  <div id="home">
    <div class="header">
      <div class="home_header_top">
        <div class="header_left" @click="showToast"></div>
        <div class="header_right">
          <p class="header_title">中原在线</p>
          <a class="header_content_right" @click="dialog_show=true">联系客服</a>
        </div>
        <div class="header_content">
          <a class="header_content_text">公告位置，实现运营人员后台可配置公告，不超过30个字符，做多支持两行显示</a>
        </div>
      </div>
    </div>
    <my-dialog :show="dialog_show" :type="'call'" :confirm_text="'呼叫'" @cancel="dialog_show=false" @confirm="confirm"
               :text="dialog_text"></my-dialog>
    <my-tab class="tab" style="top: 6rem" :data="tab_data" @clickIndex="clickIndex"></my-tab>
    <div class="content">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import myTab from '../../components/myTab'
import myDialog from '../../components/myDialog'

export default {
  name: 'home',
  components: {
    myTab,
    myDialog
  },
  data () {
    return {
      dialog_text: '95377',
      dialog_show: false,
      tab_index: 0,
      tab_data: [
        {
          name: '讨论组',
          path: 'DiscussionGroups'
        },
        {
          name: '资讯',
          path: 'information'
        },
        {
          name: '个股',
          path: 'stocks'
        },
        {
          name: '咨询',
          path: 'consulting'
        },
        {
          name: '我的',
          path: 'mine'
        },
        {
          name: '投顾',
          path: 'interest'
        }
      ]
    }
  },
  mounted () {
  },
  methods: {
    confirm () {
      this.dialog_show = false
    },
    clickIndex (item, index) {
      this.$router.push({
        name: item.path
      })
      this.tab_index = index
    },
    showToast () {
      // this.$Dialog.Toast({
      //   content: '你确定要退出吗'
      // })
      // this.$Dialog.Confirm({
      //   text: '95377',
      //   type: 'call'
      // })
    }
  }
}
</script>

<style scoped>
  #home {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }

  #home .header {
    width: 100%;
    height: 6rem;
    position: absolute;
    background-color: red;
  }

  #home .header .home_header_top {
    position: relative;
  }

  #home .header .home_header_top .header_title {
    text-align: center;
    font-size: 1.2rem;
    color: #FFFFFF;
    margin: 0 auto;
    display: inline-block;
    width: 15rem;
    position: absolute;
    left: 5rem;
    top: 0.5rem;
  }

  #home .header .home_header_top {
  }

  #home .tab {
    position: absolute;
    top: 10%;
    text-align: center;
    height: 2rem;
    line-height: 200%;
  }

  #home .header .header_right {
    display: inline-block;
    width: 80%;
  }

  #home .header .header_content {
    color: #FFFFFF;
    position: absolute;
    padding: 1rem;
    top: 2.1rem;
  }

  #home .header .header_content .header_content_text {
    font-size: 0.9rem;
  }

  #home .header .header_right .header_content_right {
    position: absolute;
    right: 1rem;
    color: #FFFFFF;
    font-size: 0.9rem;
  }

  #home .content {
    position: fixed;
    top: 8.3rem;
    width: 100%;
    height: 84%;
    left: 0;
    background-color: #9999;
  }
</style>



