<!--
 * @作者： 丁亚辉
 * @日期： 2019/11/13 17:20
-->

<template>

  <div id="myTab" ref="myTab">
    <div class="tab" ref="tab">
      <div class="tab_index" :key=index @click="clickIndex(item,index)" ref="tab_index"
           v-for="(item,index) in data">{{item.name}}
      </div>
      <div class="tab_line" ref="tab_line">
        <div class="index_line" ref="index_line"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'myTab',
  props: {
    data: Array
  },
  data () {
    return {
      tab_index: 0
    }
  },
  mounted () {
    this.init()
    // 监听动作解决刷新后移动条不跟随的问题
    window.addEventListener('unload', this.saveIndex)
  },
  destroyed () {
    window.removeEventListener('unload', this.saveIndex)
  },
  methods: {
    init () {
      // 设置每个的宽度
      let dataLength = this.data.length
      let clientWidth = this.$refs.tab.clientWidth
      let clientHeight = this.$refs.tab.clientHeight
      // 计算出长度，并防止自动四舍五入导致的溢出
      let tabWidth = parseInt((clientWidth / dataLength) * 100) / 100
      let tabIndex = this.$refs.tab_index
      for (let i of tabIndex) {
        // 在不同手机中的兼容问题之选项卡文字的宽度自适应
        i.style.width = tabWidth + 'px'
        // 在不同手机中的兼容问题之选项卡文字始终居中
        i.style.marginTop = (clientHeight - i.clientHeight) / 2 + 'px'
        // console.log(i.style.width)
      }
      // console.log(dataLength, clientWidth, tabWidth)
      // debugger
      // 设置跟随变化的选项卡条
      let indexLine = this.$refs.index_line
      indexLine.style.width = tabWidth * 0.4 + 'px'
      let localTabIndex = sessionStorage.getItem('tabIndex')
      localTabIndex = localTabIndex === null ? 0 : localTabIndex
      localTabIndex = parseInt(localTabIndex)
      tabIndex[localTabIndex].style.color = 'red'
      this.tab_index = localTabIndex
      indexLine.style.transform = 'translate(' + tabWidth * (localTabIndex + 0.3) + 'px' + ')'
      indexLine.style.webkitTransform = 'translate(' + tabWidth * (localTabIndex + 0.3) + 'px' + ')'
    },
    clickIndex (item, index) {
      let tabIndex = this.$refs.tab_index
      for (let i of tabIndex) {
        i.style.color = '#666666'
      }
      tabIndex[index].style.color = 'red'
      this.tab_index = index
      let indexLine = this.$refs.index_line
      let dataLength = this.data.length
      let clientWidth = this.$refs.tab.clientWidth
      let tabWidth = clientWidth / dataLength
      let l = tabWidth * (index + 0.3) + 'px'
      this.$emit('clickIndex', item, index)
      // 选项卡移动
      indexLine.style.transform = 'translate(' + l + ')'
      indexLine.style.webkitTransform = 'translate(' + l + ')'
      this.saveIndex()
    },
    saveIndex () {
      sessionStorage.setItem('tabIndex', this.tab_index)
    }
  }
}
</script>

<style scoped>
  #myTab {
    width: 100%;
    /*box-shadow: 2px 2px 2px 2px #aabfce;*/
    /*font-size: 1rem;*/
  }

  #myTab .tab {
    width: 100%;
    position: absolute;
    height: 100%;
  }

  #myTab .tab .tab_index {
    display: inline-block;
    color: #666666;
  }

  #myTab .tab_line {
    width: 100%;
    height: 1px;
    position: absolute;
    bottom: -3px;
    background-color: #D7D7D7;
  }

  #myTab .tab_line .index_line {
    height: 2px;
    position: absolute;
    bottom: 0;
    background-color: red;
    transition: transform 0.2s linear;
  }
</style>



